import React, { useEffect } from 'react'
import styles from './FilmModal.less'

interface ModalProps {
  isOpen: boolean; //  modal 
  onClose: () => void; //  modal 
  videoSrc: string; //  URL
}

const Modal: React.FC<ModalProps> = ({ isOpen, onClose, videoSrc }) => {
  useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = 'hidden' // 
    } else {
      document.body.style.overflow = '' // 
    }

    // 
    return () => {
      document.body.style.overflow = '' // 
    }
  }, [isOpen])

  if (!isOpen) {return null} //  modal 

  return (
    <section className={styles.modal}>
      <span className={styles.close} onClick={onClose}>
        Close
      </span>
      <div
        className={`${styles.movieBox} ${styles.centerInside}`}
        onClick={onClose}
      >
        <div className={styles.movieBoxS} onClick={(e) => e.stopPropagation()}>
          <iframe
            width="100%"
            height="100%"
            src={videoSrc}
            title="YouTube video player"
            frameBorder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
            allowFullScreen
            loading="lazy"
          ></iframe>
        </div>
      </div>
    </section>
  )
}

export default Modal
